angular.module("DraftController", []).controller("DraftController", function ($stateParams, AuthenticationService, LeagueService, DraftService, DraftFactory) {
    let vm = this;

    vm.message = "Your on the clock!";

    vm.currentUser = AuthenticationService.currentUser().then((response) => {
        vm.currentUser = response;
    });

    vm.availablePlayers = [];
    vm.currentDraft = DraftService.getDraft().then((response) => {
        console.log(response);
        vm.currentDraft = response.data.draft;
        vm.availablePlayers = response.data.players;
        // vm.currentDraft = response;
    });

    vm.draftPlayer = (id) => {
        console.log(id);
    };
});